import {
	uniBadge,uniPopup
} from '@dcloudio/uni-ui'
import {
	mapActions,
	mapState,
	mapMutations
} from 'vuex'
export default {
	components: {
		uniBadge,
		uniPopup
	},
	data() {
		return {
			scrollHeight: '500px',
			isShowCar: false,
			options: [{
				icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/carts.png',
				text: '购物车',
				info: 2
			}],
			currentGoods: {},
			buttonGroup: [{
				text: '立即购买',
				backgroundColor: '#ffa200',
				color: '#fff'
			}],
			leftArray: [],
			mainArray: [],
			topArr: [],
			leftIndex: 0,
			isMainScroll: false,
			scrollInto: '',
			tipsTop: '0px',
			url_config:''
		};
	},
	computed: {
		...mapState(['goodsList'])
	},
	onLoad(e) {
		this.url_config = getApp().globalData.url_config
		var _this = this
		uni.getSystemInfo({
			success: async (res) => {
				/* 设置当前滚动容器的高，若非窗口的告诉，请自行修改 */
				this.scrollHeight = `${res.windowHeight}px`;
				// console.log('gaodu', res.windowHeight)
			}
		});
		this.initialize()
	},
	async onPullDownRefresh() {
		await this.initialize()
		uni.stopPullDownRefresh();
	},
	methods: {
		...mapActions(['getGoodsList','deleteGoods']),
		...mapMutations(['SET_VALUE']),
		async initialize() {
			// uni.request({
			// 	url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + this.newsId,
			// 	method: 'GET',
			// 	data: {},
			// 	success: res => {
			// 		console.log(res)
			// 		this.news = res.data
			// 	},
			// 	fail: () => {},
			// 	complete: () => {}
			// });
			await this.getGoodsList({
				params: {},
				stateName: 'goodsList'
			})
			this.getListData();
		},
		radioChange: function(obj, e) {
			// console.log(obj,e.target.value)
			obj.condition = e.target.value
			console.log(obj.condition)
			// for (let i = 0; i < this.items.length; i++) {
			// 	if (this.items[i].value === evt.target.value) {
			// 		this.current = i;
			// 		break;
			// 	}
			// }
		},
		openSize(info) {
			this.currentGoods = info
			console.log(info)
			this.$refs.goodsDetail.close()
			this.$refs.goodsSize.open()
		},
		openDetail(info) {
			this.currentGoods = info
			this.SET_VALUE({key:'handleGoods',value:info})
			uni.navigateTo({
				url:"../goodsUpdate/goodsUpdate"
			})
		},
		async deleteRequest(info){
			let res = await this.deleteGoods({
				params:{
					_id:info._id
				}
			})
			console.log(res)
			if(!res){
				this.initialize()
			}
		},
		/* 获取列表数据 */
		getListData() {
			let left = [],
				main = []
			this.goodsList.forEach((e, i) => {
				if (left.indexOf(e.type) == -1) {
					left.push(e.type)
				}
			})
			// console.log('左侧菜单',left)
			// let emptyType = this.goodsList.filter(e=>!e.type)
			// // console.log('默认',emptyType)
			// main.push({
			// 	type:'默认',
			// 	list:emptyType
			// })
			left.forEach((e, i) => {
				let arr = this.goodsList.filter(p => p.type === e)
				main.push({
					type: e,
					list: arr
				})
			})
			console.log(left, main)
			// for (let i = 0; i < 8; i++) {
			// 	left.push(`${i+1}类商品`);

			// 	let list = [];
			// 	for (let j = 0; j < (i + 1); j++) {
			// 		list.push(j);
			// 	}
			// 	main.push({
			// 		title: `第${i+1}类商品标题`,
			// 		list
			// 	})
			// }
			this.leftArray = left
			this.mainArray = main
			this.$nextTick(() => {
				this.getElementTop();
			});
		},
		//获取距离顶部的高度
		getScrollTop(selector) {
			return new Promise((resolve, reject) => {
				let query = uni.createSelectorQuery().in(this);
				query.select(selector + '').boundingClientRect(data => {
					resolve(data.top)
				}).exec();
			})
		},
		getElementInfo(selector) {
			return new Promise((resolve, reject) => {
				let query = uni.createSelectorQuery().in(this);
				query.select(selector + '').boundingClientRect(data => {
					resolve(data)
				}).exec();
			})
		},
		/* 获取元素顶部信息 */
		async getElementTop() {
			/* Promise 对象数组 */
			let p_arr = [];

			/* 遍历数据，创建相应的 Promise 数组数据 */
			for (let i = 0; i < this.mainArray.length; i++) {
				const resu = await this.getScrollTop(`#item-${i}`)
				p_arr.push(resu)
			}

			// console.log('p_arr', p_arr)

			/* 主区域滚动容器的顶部距离 */
			this.getScrollTop("#scroll-el").then((res) => {

				let top = res;





				/* 所有节点信息返回后调用该方法 */
				Promise.all(p_arr).then((data) => {
					console.log('滚动', data)
					this.tipsTop = `${data}px`;
					this.topArr = data;
				});
			})
		},

		/* 主区域滚动监听 */
		mainScroll(e) {
			if (!this.isMainScroll) {
				return;
			}
			let top = e.detail.scrollTop;
			let index = -1;

			if (top >= this.topArr[this.topArr.length - 1]) {
				index = this.topArr.length - 1;
			} else {
				index = this.topArr.findIndex((item, index) => {
					return this.topArr[index + 1] >= top;
				});
			}
			this.leftIndex = (index < 0 ? 0 : index);
		},
		/* 主区域触摸 */
		mainTouch() {
			this.isMainScroll = true;
		},
		/* 左侧导航点击 */
		leftTap(e) {
			let index = e.currentTarget.dataset.index;
			this.isMainScroll = false;
			this.leftIndex = Number(index);
			this.scrollInto = `item-${index}`;
		}
	}
}
